import React from 'react';
import anime from 'animejs/lib/anime.es.js';

import "./index.css";


class Autumn extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            noOfLeaves: 0,
            sceneWidth: 0,
            sceneHeight: 0,
        }
        this.autumnRef = React.createRef();
        // to avoid errors due to inline bind returing new function each time
        this.screenSizeChangeHandler = this.screenSizeChangeHandler.bind(this);
    }

    generateAnimation(e) {
        const fallTime = anime.random(2000, 3500);
        const startY = anime.random(-this.state.sceneHeight-50, -50);
        const startX = anime.random(-10, this.state.sceneWidth);
        const rotates = {
            x: anime.random(-70, 70),   
            y: anime.random(-45, 45),
            z: anime.random(0, 360), // equalent to normal image rotate
        }
        anime({
            targets: e,
            translateX: [
                {value: startX, duration: 0, delay: 0},
                {value: startX + anime.random(-300, -50), duration: fallTime, delay: 0, easing: 'cubicBezier(0.570, 0.005, 0.585, 0.830)'}
            ],
            translateY: [
                {value: startY, duration: 0, delay: 0},
                {value:  this.state.sceneHeight , duration: fallTime, delay: 0},
            ],
            rotateX: [
                {value: rotates.x, duration: 0, delay: 0},
                {value: rotates.x + anime.random(-25, 25), duration: fallTime}
            ],
            rotateY: [
                {value: 0, duration: 0, delay: 0},
                {value: rotates.y, duration: fallTime}
            ],
            rotateZ: [
                {value: rotates.z, duration: 0, delay: 0},
                {value: rotates.z + anime.random(-90, 90), duration: fallTime}
            ],          
            easing: 'linear',
            complete: this.generateAnimation.bind(this,e),
        });
    }

    removeOldAnimations(){
        // remove all old animes
        const animationLeaves = document.getElementsByClassName("autumn-leaf");
        for (let i=0; i<animationLeaves.length; i++){
            anime.remove(animationLeaves[i]);
        }
    }

    screenSizeChangeHandler(){

        this.removeOldAnimations();

        const sceneHeight = this.autumnRef.current.clientHeight;
        const sceneWidth = this.autumnRef.current.clientWidth;

        this.setState({
            noOfLeaves: Math.floor(sceneWidth/30),
            sceneHeight,
            sceneWidth,
        });
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.screenSizeChangeHandler);
    }

    animeStart(){
        const animationLeaves = document.getElementsByClassName("autumn-leaf");
        for (let i=0; i<animationLeaves.length; i++){
            this.generateAnimation(animationLeaves[i]);
        }
    }

    componentDidUpdate(){
        this.animeStart();
    }
    
    componentDidMount() {
        this.screenSizeChangeHandler();
        window.addEventListener('resize', this.screenSizeChangeHandler);
    }

    render(){

        let leaves = [];
        this.removeOldAnimations();

        for (let i=0 ; i< this.state.noOfLeaves; i++){
            leaves.push(
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 26.458333 26.458334"
                    height="50"
                    width="50"
                    key={i}
                    className="autumn-leaf">
                    <path
                        transform="translate(0,-270.54165)"
                        fill="#942107"
                        d="m 13.188743,274.77317 c -8.7e-5,3.25329 2.806609,2.92829 3.233453,0.96674 0.101266,-0.16657 0.180096,-0.24617 0.159497,0.0595 -0.289632,1.43945 -0.192751,2.82958 0.256155,3.89658 -0.559114,2.70963 0.998434,3.37793 2.382807,1.10072 0.218401,0.16471 0.629659,-0.37394 1.014985,-0.81801 1.061003,1.5101 2.358361,-0.23678 2.856457,0.2677 l 2.464964,-0.8775 c 0.456722,-0.19268 0.95769,-0.0591 0.956986,0.10412 0.146203,-0.22388 -1.345171,0.18271 -1.652976,1.21958 -0.923063,1.1594 -0.216211,3.16771 0.07975,2.97458 -2.208197,2.96976 -1.014914,3.69771 0.496724,4.73425 -0.50012,0.032 -1.03423,-0.2149 -1.485586,0.21725 l -1.758847,1.39923 c -3.198223,0.96904 -2.32078,2.43249 -1.232482,3.91158 0.640347,0.33433 1.302852,0.78231 1.870472,0.74364 l -0.144998,0.20822 c -1.984484,-0.57701 -3.575907,0.23211 -5.140335,0.17315 -0.773295,-0.0716 -1.334157,-0.3029 -1.486067,0.37715 -0.732146,-0.15685 -1.341304,-0.67113 -1.841474,-1.50216 0.0348,-0.49991 -0.09824,-0.99232 -0.507491,-1.47242 l -0.375158,3.5126 -0.239887,0.0633 c 0.191874,-1.25776 0.365256,-2.50248 0.341995,-3.60852 l -0.147444,0.0326 c -0.183392,1.44873 -2.155343,2.67922 -2.464964,1.53191 l -1.8704717,0.4908 c -1.256159,-0.6761 -2.597121,0.21651 -4.422436,0.66928 -0.305575,0.004 -0.266432,-0.0935 -0.0725,-0.1636 0.666381,-0.22172 1.508377,-0.82158 1.935723,-0.92212 0.346217,-0.50793 0.553928,-0.85335 1.239731,-0.8775 -0.03056,-1.77115 -1.392266,-0.97532 -2.74046,-1.5914 -0.997555,-0.35144 -1.819672,-0.75429 -3.189953,-0.99648 -0.308521,-0.16473 -0.03748,-0.16734 0.0435,-0.22311 1.890506,-0.49189 2.205516,-1.20735 1.83231,-2.04551 -0.250285,-0.5621 -0.810098,-1.17938 -1.411816,-1.82144 0.968214,-1.58966 -0.898142,-3.30648 -2.05896997,-3.58437 -0.277196,-0.28847 0.095,-0.14251 0.217497,-0.1636 1.19501797,0.21179 4.39612597,1.61714 5.27470597,-0.23977 0.735033,0.45364 1.430712,0.76494 2.120185,1.39985 0.709515,0.13629 0.572327,-0.26187 0.608991,-0.55029 -0.497077,-1.51924 -0.890638,-2.43519 -1.232481,-3.9562 l -0.695991,-0.90724 c -0.110029,-0.19569 -0.04323,-0.23177 0.188498,-0.11899 1.687852,0.81843 2.575757,0.24094 2.473457,-2.12247 1.4093607,-1.07909 1.2847487,-1.9418 1.3689857,-3.51436 0.09395,-1.15384 0.248098,-1.04676 0.478493,0 0.190705,1.24368 0.990075,2.49839 2.276467,2.02271 z"
                    />

                </svg>
            );
        }
        return (
            <div id="autumn" ref={this.autumnRef}>
                {leaves}
            </div>
        )
    }
   
}

export default Autumn;